.total-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 20px; */
  background-color: #151515;
  color: white;
  min-height: 100vh;
  box-sizing: border-box;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 12px;
  /* 컨테이너 패딩: 거터의 절반 */
}

.page-title {
  font-size: 1.6em;
  color: #434BDB;
  text-align: center;
  margin: 60px 0 30px 0;
}

.main-container {
  width: 80%;
}

.main-title {
  font-size: 3em;
  text-align: center;
  margin-bottom: 60px;
}

.research-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.research-title h1 {
  font-size: 1.8em;
  margin-bottom: 10px;
}

.research-contents {
  background-color: #333;
  padding: 15px;
  border-radius: 8px;
  color: #ccc;
  font-size: 1.4em;
}

/* Tablet 버전 (768px - 991px) */
@media (max-width: 991px) {
  .main-container {
    width: 90%;
    padding: 0 10px;
  }

  .page-title {
    font-size: 1.4em;
  }

  .main-title {
    font-size: 2.5em;
  }

  .research-title h1 {
    font-size: 1.5em;
  }

  .research-contents {
    font-size: 1.2em;
  }
}

/* Mobile 버전 (최대 767px) */
@media (max-width: 767px) {
  .main-container {
    width: 100%;
    padding: 0 10px;
  }

  .page-title {
    font-size: 1.2em;
  }

  .main-title {
    font-size: 2em;
    margin-bottom: 30px;
  }

  .research-title h1 {
    font-size: 1.25em;
  }

  .research-contents {
    font-size: 1em;
    padding: 10px;
  }
}